import c from "./HourlyWeather.module.css";
import React from "react";


const HourlyWeather = (props) => {
    return (<div>
        <hr/>
        <div className={c.hourlyTitle}>Прогноз на наступні 24 години</div>
        <div className={c.flexScrollContainer}>
            <div className={c.weatherForFewDaysMainContainer}>
                {props.hourlyWeatherContainer.map(el => <div key={el.timestamp_local}>
                    <div className={c.weatherForFewDaysLittleContainer}>
                        <div>{el.timestamp_local.substr(11, 2)}<span className={c.minutes}>:00</span></div>
                        <div className={c.iconContainer}><img
                            src={`https://www.weatherbit.io/static/img/icons/${el.weather.icon}.png`} alt={'icon'}/></div>
                        <div className={c.temp}>{Math.round(el.temp)}&#176;</div>
                    </div>
                </div>)}
            </div>
        </div>
    </div>);
}

export default HourlyWeather