import React, {useEffect} from 'react';
import './App.css';
import MainPage from "./Components/MainPage/MainPageContainer";
import Header from "./Components/Header/HeaderContainer";
import Bottom from "./Components/Bottom/BottomContainer";
import dayImage from './images/bg22.jpg';
import nightImage from './images/n2.jpg';
import {Route} from "react-router-dom";
import Preloader from "./Components/Preloader/Preloader";
import {connect} from "react-redux";

function App(props) {
    useEffect(() => {
        const currentHour = new Date().getHours();
        if (currentHour < 20 && currentHour > 7) {
            document.body.style.background = `url(${dayImage}) no-repeat`;
            document.body.style.backgroundSize = `cover`;
        } else {
            document.body.style.background = `url(${nightImage})`;
            document.body.style.backgroundSize = `cover`;
        }
    }, []);
    return (
        <div className="App">
            {props.isFeaching && <Preloader/>}
            <Header/>
            <Route path='/:city?' render={() => <MainPage/>}/>
            <Bottom/>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        isFeaching: state.main.isFeaching
    }
}

export default connect(mapStateToProps, {})(App);

