import React from "react";
import c from "./MainWeather.module.css";

const MainWeather = (props) => {
    return (<div className={c.mainWeatherContainer}>
        {
            props.currentWeather.map(el => <div key={el.city_name}>
                <div className={c.cityNameTitle}>{el.city_name}</div>
                <div>{el.weather.description}</div>
                <div className={c.mainTemperatureValue}>{Math.round(el.temp)}&#176;</div>
            </div>)
        }
    </div>);
}

export default MainWeather;