import * as axios from "axios";

const KEY = 'b53365960c064944b5b93c7f84ad25ca';

const serv = axios.create({
    baseURL: 'http://api.weatherbit.io/v2.0/'
}, [])

export const getCurrentWeatherApi = (city) => {
    return serv.get(`current?city=${city}&lang=uk&key=${KEY}`)
}
export const getDailyWeatherApi = (city) => {
    return serv.get(`forecast/daily?city=${city}&lang=uk&key=${KEY}`)
}

export const getHourlyWeatherApi = (city) => {
    return serv.get(`forecast/hourly?city=${city}&hours=24&lang=uk&key=${KEY}`)
}
