import React from "react";
import c from './DailyWeather.module.css';

const dayArray = ["Неділя", "Понеділок", "Вівторок", "Середа", "Четвер", "П`ятниця", "Субота"]

const DailyWeather = (props) => {
    const getDate = (date) => {
        date = new Date(date);
        const year = date.getFullYear();
        let month = date.getMonth();
        month = +month + 1;
        if (month < 10) {
            month = '0' + month;
        }
        const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        date = day + '.' + month + '.' + year;
        return date;
    }
    return (<div className={c.dailyPage}>
        <hr/>
        <div className={c.dailyTitle}>Прогноз на наступні 16 днів</div>
        <div className={c.weatherForFewDaysMainContainer}>
            {props.dailyWeatherContainer.map(el => <div className={c.oneDayContainer} key={el.datetime}>
                <div
                    className={c.gridCont}>{dayArray[new Date(el.datetime).getDay()]}</div>
                <div
                    className={c.dataDiv}>{getDate(el.datetime)}</div>
                <div className={c.iconContainer}><img
                    src={`https://www.weatherbit.io/static/img/icons/${el.weather.icon}.png`} alt={'a'}/></div>
                <div className={c.gridCont}>{Math.round(el.high_temp)}&#176;</div>
                <div className={`${c.gridCont}+ ' ' + ${c.darken} `}>{Math.round(el.low_temp)}&#176;</div>
            </div>)}
        </div>
    </div>);
}

export default DailyWeather;