import {getCurrentWeatherApi, getDailyWeatherApi, getHourlyWeatherApi} from "../Api/Api";

const CURRENTWEATHER = 'currentWeather';
const SETERROR = 'setError';
const DELETEERROR = 'deleteError';
const PRELOADER = 'preLoader';

const initionalState = {
    currentWeather: [],
    dailyWeatherContainer: [],
    hourlyWeatherContainer: [],
    someErrorOccured: false,
    errorMessage: '',
    isFeaching: false
};

export const mainPageReducer = (state = initionalState, action) => {
    switch (action.type) {
        case CURRENTWEATHER:
            return {
                ...state,
                currentWeather: action.current,
                dailyWeatherContainer: action.daily,
                hourlyWeatherContainer: action.hourly
            }
        case SETERROR:
            return {
                ...state,
                someErrorOccured: true,
                errorMessage: action.text
            }
        case PRELOADER:
            return {
                ...state,
                isFeaching: action.isFeaching
            }
        case DELETEERROR:
            return {
                ...state,
                someErrorOccured: false
            }

        default:
            return state;
    }
}

const addError = (text) => {
    return {type: SETERROR, text};
}

export const deleteError = () => {
    return {type: DELETEERROR};
}

const setDataForCurrentWeather = (current, daily, hourly) => {
    return {type: CURRENTWEATHER, current, daily, hourly};
}

export const getCurrentWeather = (city) => async (dispatch) => {
    if (city === undefined || city.trim() === '') {
        dispatch(addError('Поле пошуку міста не може бути пустим:)'));
    } else {
        dispatch(isPreLoader(true));
        const current_weather_response = await getCurrentWeatherApi(city);
        const daily_weather_response = await getDailyWeatherApi(city);
        const hourly_weather_response = await getHourlyWeatherApi(city);
        dispatch(isPreLoader(false));
        if (current_weather_response.status === 200 && daily_weather_response.status === 200 && hourly_weather_response.status === 200) {
            dispatch(setDataForCurrentWeather(current_weather_response.data.data, daily_weather_response.data.data, hourly_weather_response.data.data));
        } else if (current_weather_response.status === 204 || daily_weather_response.status === 204 || hourly_weather_response.status === 204) {
            dispatch(addError('Дане місто нажаль не знайдене. Можливо Ви допустили помилку. Спробуйте будь-ласка ще раз. '));
        } else {
            dispatch(addError('Нажаль сталась якась помилка, спробуйте будь-ласка пізніше'));
        }
    }
}

export const isPreLoader = (isFeaching) => {
    return {type: PRELOADER, isFeaching}
}
