import React from "react";
import Modal from 'react-modal';
import c from './ErrorModalWindow.module.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: "#2581b7"
    },
    overlay: {zIndex: 1000}
};

const ErrorModalWindow = (props) => {
    return <Modal
        isOpen={props.modalIsOpen}
        onRequestClose={props.closeModal}
        ariaHideApp={false}
        style={customStyles}
        contentLabel='Cart Modal'
    >
        <div className={c.modal}>
            <div><span className={c.button} onClick={props.closeModal}>&#10006;</span></div>
            <div className={c.text}> {props.text}</div>
            <div>
                <button className={c.close} onClick={props.closeModal}>Закрити</button>
            </div>
        </div>
    </Modal>
}
export default ErrorModalWindow;