import React, {useState} from "react";
import c from './MainPage.module.css';
import HourlyWeather from "./MainPageParts/HourlyWeather/HourlyWeather";
import MainWeather from "./MainPageParts/MainWeather/MainWeather";
import TodaysWeatherData from "./MainPageParts/TodaysWeatherData/TodaysWeatherData";
import arrowUp from "../../images/arrowup.png";
import arrowDown from "../../images/arrowdown.png";

const MainPage = (props) => {
    const [openDescpription, setOpenDescpription] = useState(false);
    const toggleOpenDescription = () => {
        openDescpription ? setOpenDescpription(false) : setOpenDescpription(true);
    }
    return (<div className={c.page}>
        <MainWeather currentWeather={props.currentWeather}/>
        {!openDescpription && <div className={c.toogleButton}> <span onClick={toggleOpenDescription}><div
            className={c.arrowContainer}> <img src={arrowDown} alt='img'/></div></span></div>}
        {openDescpription &&
        <div className={c.openDescription}><TodaysWeatherData currentWeather={props.currentWeather}/></div>}
        {<div className={c.pcMode}><TodaysWeatherData currentWeather={props.currentWeather}/></div>}
        {!openDescpription && <div className={c.closeDescription}></div>}
        {openDescpription && <div className={c.toogleButton}> <span onClick={toggleOpenDescription}><div
            className={c.arrowContainer}> <img src={arrowUp} alt='img'/></div></span></div>}
        <HourlyWeather hourlyWeatherContainer={props.hourlyWeatherContainer}/>
    </div>);
}

export default MainPage;
