import React, {useEffect} from "react";
import MainPage from "./MainPage";
import {getCurrentWeather} from "../Redux/MainPageReducer";
import {connect} from "react-redux";
import {compose} from "redux";
import {withRouter} from "react-router-dom";

const DEFAULTCITY = 'Львів';

const MainPageContainer = (props) => {
    useEffect(() => {
        if (props.match.params.city === undefined) {
            props.getCurrentWeather(DEFAULTCITY);
        } else {
            props.getCurrentWeather(props.match.params.city);
        }
    }, [props.match.params.city]);

    return (<MainPage getCurrentWeather={props.getCurrentWeather} currentWeather={props.currentWeather}
                      dailyWeatherContainer={props.dailyWeatherContainer} getHourlyWeather={props.getHourlyWeather}
                      hourlyWeatherContainer={props.hourlyWeatherContainer}/>);
}

const mapStateToProps = (state) => {
    return {
        currentWeather: state.main.currentWeather,
        hourlyWeatherContainer: state.main.hourlyWeatherContainer
    }
}

export default compose(
    withRouter,
    connect(mapStateToProps, {getCurrentWeather}))
(MainPageContainer);
