import React from "react";
import c from '../Header.module.css';
import searchIcon from '../../../images/search.png';
import {Field, reduxForm} from 'redux-form';
import {Input} from "./InputComponent/InputComponent";

const Form = (props) => {
    return (<form onSubmit={props.handleSubmit} className={c.searchForm}>
        <div className={c.searchPlaceInputContainer}>
            <Field component={Input} name={'search'} placeholder={'...Ведіть місто'}/>
        </div>
        <div className={c.searchIconDiv}>
            <button>
                <div className={c.searchIconContainer}><img src={searchIcon} alt='img'/></div>
            </button>
        </div>
    </form>);
}

const SearchForm = reduxForm({
    form: 'search'
})(Form);

export default SearchForm;
