import React from "react";
import c from './Header.module.css';
import logo from '../../images/logo1.PNG';
import SearchForm from "./Form/Form";
import ErrorModalWindow from "../ErrorModalWindow/ErrorModalWindow";

const Header = (props) => {
    const onSubmit = (formData) => {
        props.getCurrentWeather(formData.search);
    }
    return (<div className={c.page}>
        <div className={c.headerContainer}>
            <div className={c.logoContainer}><img src={logo} alt='logo'/></div>
            <SearchForm onSubmit={onSubmit}/>
        </div>
        <ErrorModalWindow text={props.errorMessage} modalIsOpen={props.someErrorOccured}
                          closeModal={props.deleteError}/>
    </div>);
}

export default Header;
