import {deleteError, getCurrentWeather} from "../Redux/MainPageReducer";
import {connect} from "react-redux";
import Header from "./Header";

const mapStateToProps = (state) => {
    return {
        currentWeather: state.main.currentWeather,
        someErrorOccured: state.main.someErrorOccured,
        errorMessage: state.main.errorMessage
    }
}

export default connect(mapStateToProps, {getCurrentWeather, deleteError})(Header);


