import React from "react";
import c from './Preloader.module.css';

const Preloader = () => {
    return <div className={c.body}>
        <img className={c.img} alt={'Loading...'} src={'https://pascnet.org/wp-content/plugins/use-your-drive/css/clouds/cloud_loading_256.gif'}/>
    </div>
}

export default Preloader;