import React from "react";
import c from "./TodaysWeatherData.module.css";

const TodaysWeatherData = (props) => {
    const changeTime = (date) => {
        let newDateArr = date.split(':');
        return +newDateArr[0] + 3 + ':' + newDateArr[1];
    }

    return (<div className={c.mainWeatherContainer}>
        {
            props.currentWeather.map(el => <div className={c.flexContainer} key={el.app_temp}>
                <div className={c.text}>Атмосферний тиск: <b>{Math.round(el.pres)}</b></div>
                <div className={c.text}>Схід сонця: <b>{changeTime(el.sunrise)}</b></div>
                <div className={c.text}>Відчувається як: <b>{el.app_temp}&#176;</b></div>
                <div className={c.text}>Швидкість вітру: <b>{Math.round(el.wind_spd)}км./год.</b></div>
                <div className={c.text}>Напрям вітру: <b>{el.wind_cdir_full}</b></div>
                <div className={c.text}>Видимість: <b>{Math.round(el.vis)}км.</b></div>
                <div className={c.text}>Захід сонця: <b>{changeTime(el.sunset)}</b></div>
                <div className={c.text}>Відносна вологість: <b>{el.rh}%</b></div>
                <div className={c.text}>Хмарність: <b>{el.clouds}%</b></div>
                <div className={c.text}>Опади: <b>{el.precip}мм/год</b></div>
            </div>)
        }
    </div>);
}

export default TodaysWeatherData;